<template>
    <div class="filters">
        <v-input
            class="filter"
            v-model="filters.name"
            :placeholder="$t('NAME')"
            :debounce="500"
            clearable
        />

        <v-select
            class="filter"
            v-model="filters.genre_id"
            :placeholder="$t('CATEGORY')"
            :options="genresOptions"
            clearable
        />

        <v-input
            class="filter"
            v-model="filters.developer_name"
            :placeholder="$t('DEVELOPER')"
            :debounce="500"
            clearable
        />

        <v-select
            class="filter"
            v-model="filters.type"
            :placeholder="$t('TYPE')"
            :options="typesOptions"
            clearable
        />

        <v-select
            class="filter"
            v-model="filters.compatibility"
            :placeholder="$t('DEVICE')"
            :options="devicesOptions"
            clearable
        />

        <v-input
            class="filter"
            v-model="filters.source_name"
            :placeholder="$t('SOURCE')"
            :debounce="500"
            clearable
        />

        <v-button
          v-if="saveToUrl"
          class="filters__button"
          type="primary"
          @click.native="$modal.show('sharePage')"
        >
          {{ $t('SHARE_YOUR_SEARCH') }}
        </v-button>
        <share-page-modal />
    </div>
</template>

<script>
import VInput from '@/components/VInput.vue';
import VSelect from '@/components/VSelect.vue';
import VButton from '@/components/Button.vue';
import { mapGetters, mapState } from 'vuex';
import { clone, isEqual } from 'lodash';
import { operatingSystems } from '@/data';
import SharePageModal from '@/components/modals/SharePageModal.vue';

export default {
  components: {
    VInput,
    VSelect,
    VButton,
    SharePageModal,
  },
  props: {
    value: Object,
    saveToUrl: Boolean,
  },
  data() {
    return {
      filters: {
        name: '',
        genre_id: null,
        developer_name: '',
        type: '',
        compatibility: '',
        source_name: '',
      },
      devicesOptions: operatingSystems.map(({ name, device }) => ({ label: device, value: name })),
    };
  },
  computed: {
    ...mapState(['genres']),
    ...mapGetters(['currentDeviceOS']),
    genresOptions() {
      return Object.values(this.genres.official).map(genre => ({ label: genre.name, value: genre.id }));
    },
    typesOptions() {
      return ['official_app', 'user_app', 'repo_app', 'enhancement'].map(type => ({ label: this.$t(type.toUpperCase()), value: type }));
    },
  },
  methods: {
    trySetUrl() {
      if (!this.saveToUrl) {
        return;
      }

      const filtersQuery = Object.fromEntries(Object.entries(this.filters).filter(([, value]) => value).map(([key, value]) => [key, value.toString()]));
      const notFiltersQuery = Object.fromEntries(Object.entries(this.$route.query).filter(([param]) => !(param in this.filters)));
      const newQuery = { ...filtersQuery, ...notFiltersQuery };
      if (isEqual(this.$route.query, newQuery)) {
        return;
      }

      this.$router.push({ path: this.$route.path, query: newQuery });
    },
    tryParseUrl() {
      if (!this.saveToUrl) {
        return;
      }

      for (const filter in this.filters) {
        this.filters[filter] = this.parseQueryParam(this.$route.query, filter);
      }
    },
    parseQueryParam(query, param) {
      switch (param) {
        case 'genre_id':
          return +query[param] || null;
        default:
          return query[param];
      }
    },
  },
  watch: {
    $route: {
      handler(oldRoute, newRoute) {
        if (oldRoute.name !== newRoute.name) {
          return;
        }

        if (!isEqual(oldRoute.query, newRoute.query)) {
          this.tryParseUrl();
        }
      },
      deep: true,
    },
    value: {
      handler() {
        if (isEqual(this.filters, this.value)) {
          return;
        }

        this.filters = clone(this.value);
      },
      deep: true,
    },
    filters: {
      handler() {
        this.$emit('input', clone(this.filters));
        this.trySetUrl();
      },
      deep: true,
    },
    currentDeviceOS: {
      handler() {
        if (this.filters.compatibility) {
          return;
        }

        this.filters.compatibility = this.currentDeviceOS;
      },
      immediate: true,
    },
  },
  created() {
    this.tryParseUrl();
  },
};
</script>

