      grid-auto-rows: 1fr;
    }

    &__button {
      @include container(search, 1300px) {
        grid-column: span 2;
      }

      @include notebook {
        font-size: 16px;
      }
    }
}

.filter {
  flex: 1 0 0;
  min-width: 170px;
}
</style>
